.editorContainer {
  position: fixed!important;
  top: 0;
  left: 0;
  z-index: 2147483647;
  width: 100vw;
  height: 100vh;
  box-sizing: border-box;
}

.buttonsContainer {
  border-radius: 1000px;
  z-index: 2147483647;
  background-color: var(--grayscale-gs-white);
  position: fixed;
  bottom: 12px;
  left: 12px;
  transition: .3s;
  transform: translateY(0);

  &_hidden {
    transform: translateY(60px);
  }

  & .expandButton {
    position: absolute;
    left: calc(50% - 8px);
    top: -24px;
    transform: scaleX(2);
    cursor: pointer;
    transition: .3s;
    opacity: .5;
    filter: drop-shadow(0 0 2px var(--black));

    & * {
      color: var(--grayscale-gs-white);
      fill: var(--grayscale-gs-white);
    }

    &_hidden {
      transform: scaleX(2) rotate(180deg);
    }
  }

  & .colorPicker *, & .colorPicker {
    border-radius: 1000px!important;
    width: 24px!important;
    height: 24px!important;
  }
}

.colorPickerRoot {
  z-index: 2147483647;
}
.canvasContainer {
  pointer-events: none;

  &_active {
    pointer-events: visible;
  }
}

.circle {
  position: absolute;
  width: 20px;
  height: 20px;
  border: 2px solid red;
  border-radius: 50%;
  pointer-events: none;
  z-index: 2147483647;
  animation-name: scaleFrom0To1;
  animation-duration: .3s;
  opacity: 0;
  transform: scale(0.01);
}

@keyframes scaleFrom0To1 {
  from {
    transform: scale(0.01);
    opacity: 1;
  }
  to {
    transform: scale(1);
    opacity: 0;
  }
}