.container {
  position: fixed!important;
  top: 0;
  left: 0;
  z-index: 2147483647;
  width: 100vw;
  height: 100vh;
  box-sizing: border-box;
  visibility: hidden;
}

.iFrame {
  border: none;
  width: 100%;
  height: 100%;
  transform: scaleX(-1);
}

.drag {
  border-radius: 300px;
  width: 200px!important;
  height: 200px!important;
  overflow: hidden;
  visibility: visible;
}

.dragDiv {
  border-radius: 300px;
  width: 100%;
  height: 100%;
  z-index: 1;
  position: absolute;
  top: 0;
  left: 0;
}