.cropper {
  position: fixed!important;
  top: 0;
  left: 0;
  z-index: 2147483647;
  width: 100vw;
  height: 100vh;
  box-sizing: border-box;

  &_background {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    box-sizing: border-box;
  }

  &_buttonContainer {
    z-index: 2147483647;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    box-sizing: border-box;
  }
}